 my best president! After that to fulfil my little dream, I decided to lead a Club with 30 years of Legacy with the youngest and the newest team. Last but not the least, I would like to thank Rtr. Arshad anna for his support all the time!! Let's together Celebrate Rotaract! ")])])])],1),_c('div',{staticClass:"my-5"}),_c('Bulletin'),_c('v-layout',{staticClass:"grey lighten-2 py-5 px-3",attrs:{"align-center":"","justify-center":"","wrap":""}},[_c('v-flex',{attrs:{"xs12":"","md4":"","my-3":""}},[_c('div',{staticStyle:{"display":"flex","justify-content":"center"}},[_c('div',{staticClass:"mr-5",staticStyle:{"border":"1px solid white"}}),_c('center',[_c('div',{staticClass:"count"},[_vm._v(" 25+ members ")])]),_c('div',{staticClass:"ml-5",staticStyle:{"border":"1px solid white"}})],1)]),_c('v-flex',{attrs:{"xs12":"","md4":"","my-3":""}},[_c('div',{staticStyle:{"display":"flex","justify-content":"center"}},[_c('div',{staticClass:"mr-5",staticStyle:{"border":"1px solid white"}}),_c('center',[_c('div',{staticClass:"count"},[_vm._v(" 30 past presidents ")])]),_c('div',{staticClass:"ml-5",staticStyle:{"border":"1px solid white"}})],1)]),_c('v-flex',{attrs:{"xs12":"","md4":"","my-3":""}},[_c('div',{staticStyle:{"display":"flex","justify-content":"center"}},[_c('div',{staticClass:"mr-5",staticStyle:{"border":"1px solid white"}}),_c('center',[_c('div',{staticClass:"count"},[_vm._v(" 3 DRR"),_c('span',{staticStyle:{"text-transform":"lowercase"}},[_vm._v("s")])])]),_c('div',{staticClass:"ml-5",staticStyle:{"border":"1px solid white"}})],1)])],1),_c('center',[_c('div',{staticClass:"mt-5 py-2"},[_c('span',{staticClass:"myHeadingBolder"},[_vm._v("Majestic")]),_c('span',{staticClass:"myHeadingLinear"},[_vm._v("Drr"),_c('span',{staticStyle:{"text-transform":"lowercase"}},[_vm._v("s")])])])]),_c('v-layout',{attrs:{"align-center":"","justify-space-around":"","row":"","wrap":"","mx-4":""}},_vm._l((_vm.drr),function(i,j){return _c('v-flex',{key:j,attrs:{"ma-2":""}},[_c('center',[_c('div',{st